.slideshow {
  display: grid;
  place-items: start;
}

.slideshow--center {
  justify-content: center;
}

.slideshow__slide {
  grid-column: 1/2;
  grid-row: 1/2;
  opacity: 0;
  transition: all var(--transition--medium);
  z-index: -1;
}

.slideshow__slide[data-status=active] {
  opacity: 1;
  z-index: 1;
}

.slideshow__controls {
  align-items: center;
  display: flex;
  grid-column: 1/2;
  grid-row: 1/2;
  height: 100%;
  justify-content: space-between;
  width: 100%;
  z-index: 2;
}

.slideshow__prev,
.slideshow__next {
  background-color: rgba(255, 255, 255, 0.5);
  border: 0;
  cursor: pointer;
  font-size: 0;
  height: 50px;
  width: 50px;
}
.slideshow__prev:before,
.slideshow__next:before {
  font-family: "icons";
  font-size: var(--wp--preset--font-size--x-large);
}

.slideshow__prev:before {
  content: "\e804";
}

.slideshow__next:before {
  content: "\e807";
}

.slideshow__controls--rounded .slideshow__prev,
.slideshow__controls--rounded .slideshow__next {
  border-radius: 12px;
}

.slideshow__controls--circular .slideshow__prev,
.slideshow__controls--circular .slideshow__next {
  border-radius: 100%;
}

.slideshow__dots {
  display: flex;
  gap: 0.25rem;
  margin-inline: auto;
  margin-top: 0.5rem;
}

.slideshow__dot {
  cursor: pointer;
  height: 1rem;
  border: 0;
  border-radius: 1rem;
  width: 1rem;
}

.slideshow__dot[data-status=active] {
  background-color: var(--wp--preset--color--100);
}

.slideshow--circular img {
  border-radius: 100%;
  border: clamp(0.25em, 2vw, 1em) solid var(--c--grey-100);
}

.slideshow--rounded img {
  border-radius: 2rem;
  border: clamp(0.25em, 2vw, 1em) solid var(--c--grey-100);
}

.slideshow--center {
  justify-content: center;
}

.slideshow--right {
  justify-content: flex-end;
}